<template>
  <div id="app">
    <div>
      <transition name="fade">
        <Carregando v-show="carregando" />
      </transition>
      <div v-show="!carregando">
        <Header />

        <router-view></router-view>
      </div>
      <Footer />
    </div>
  </div>
</template>

<script>
import Header from "@/components/layout/Header.vue";
import Footer from "@/components/layout/Footer.vue";
import Carregando from "@/components/layout/Carregando.vue";
import axios from "axios";

export default {
  name: "App",

  components: {
    Footer,
    Carregando,
    Header,
  },

  data() {
    return {
      carregando: true,
    };
  },

  mounted() {

    axios.get("http://127.0.0.1:8000/alunos")
    .then((response) => {
      console.log(response);

    });


    setTimeout(() => {
      this.carregando = false;
    }, 1500);
  },
};
</script>

